.centered{text-align:center}ul.ks-cboxtags{display:inline-flex;flex-flow:wrap}.form{max-width:600px;margin:auto}.form-group{margin:auto}.form-control{text-align:center}body,html{margin:0;padding:0}body{align-items:center;justify-content:center;height:100vh}.container{max-width:640px;font-family:"Segoe UI",Tahoma,Geneva,Verdana,sans-serif;font-size:13px}ul.ks-cboxtags{list-style:none;padding:20px}ul.ks-cboxtags li{display:inline}ul.ks-cboxtags li label{display:inline-block;background-color:rgba(255,255,255,.9);border:2px solid #000;border-radius:25px;white-space:nowrap;margin:3px 0px;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;-webkit-tap-highlight-color:rgba(0,0,0,0);transition:all .2s}ul.ks-cboxtags li label{padding:8px 12px;cursor:pointer}ul.ks-cboxtags li label::before{display:inline-block;font-style:normal;font-variant:normal;text-rendering:auto;-webkit-font-smoothing:antialiased;font-family:"Font Awesome 5 Free";font-weight:900;font-size:12px;padding:2px 6px 2px 2px;content:"";transition:transform .3s ease-in-out}ul.ks-cboxtags li input[type=checkbox]:checked+label::before{content:"";transform:rotate(-360deg);transition:transform .3s ease-in-out}ul.ks-cboxtags li input[type=checkbox]:checked+hover+label::before{content:"";transform:rotate(-360deg);transition:transform .3s ease-in-out}ul.ks-cboxtags li input[type=checkbox]:checked+label{border:2px solid green;background-color:#000;color:#fff;transition:all .2s}ul.ks-cboxtags li input[type=checkbox]{display:absolute}ul.ks-cboxtags li input[type=checkbox]{position:absolute;opacity:0}ul.ks-cboxtags li input[type=checkbox]:hover+label{border:2px solid green}